*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html,body{
  width: 100%;
  height: 100%;
}

h3{
  background: #f0f0f0;
  padding: 10px;
}

#app{
  display: flex;
  width: 100%;
  height: 100%;

  .left{
    background-color: #f0f0f0;
    flex: 0 0 200px;
    h3{
      background-color: coral;
      padding: 10px;
    }
    li{
      padding: 5px;
    }
    li:hover{
      background-color: lightcoral;
      cursor: pointer;
    }
  }
  .right{
    flex: 1;
    min-height: 100%;

    .array-item{
      display: inline-block;
      border: 1px #eee solid;
      padding: 10px;
      margin: 5px;
      background: #ddd;

      &.active{
        background-color: rgb(230, 230, 69);
      }
    }

    .source{
      padding: 10px;
      display: flex;
    }

  }
}